import {Component, Input, ChangeDetectionStrategy, ChangeDetectorRef, ContentChild, ElementRef, Renderer, TemplateRef, OnChanges} from '@angular/core';
import {uniqueId} from '../../util/util';
import {NglFormInput} from './input';
import {NglFormLabelTemplate, getFormLabel} from '../form-label';


export class NglFormElement implements OnChanges {
   contentEl: NglFormInput;

   labelStr: string;
   labelTpl: NglFormLabelTemplate;

   error: string;

  uid = uniqueId('form_element');

  required = false;

  _label: TemplateRef<any> | string;

  constructor(public detector: ChangeDetectorRef, private element: ElementRef, private renderer: Renderer) {}

  ngOnInit() {
    this.renderer.setElementClass(this.element.nativeElement, 'slds-form-element', true);
  }

  ngOnChanges(changes?: any) {
    this.setFormLabel();
    this.setInputErrorId();
    this.renderer.setElementClass(this.element.nativeElement, 'slds-has-error', !!this.error);
  }

  ngAfterContentInit() {
    if (!this.contentEl) {
      throw Error(`Couldn't find an input, textarea or select with [nglFormControl] attribute inside <ngl-form-element>`);
    }
    this.contentEl.id = this.uid;
    this.setInputErrorId();
    this.setFormLabel();
  }

  private setInputErrorId() {
    if (!this.contentEl) return;
    this.contentEl.describedBy = this.error ? `error_${this.uid}` : null;
  }

  private setFormLabel() {
    this._label = getFormLabel(this.labelStr, this.labelTpl);
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'ngl-form-element',
  template: `
    <label class="slds-form-element__label" [attr.for]="uid"><abbr class="slds-required" *ngIf="required" title="required">*</abbr><span [nglInternalOutlet]="_label"></span></label><div class="slds-form-element__control"><ng-content></ng-content></div><div class="slds-form-element__help" *ngIf="error" [id]="'error_' + uid">{{error}}</div>
  `,
  changeDetection: ChangeDetectionStrategy.OnPush,
  styles: [`:host { display: block; }`],
}, ] },
];
/** @nocollapse */
static ctorParameters: ({type: any, decorators?: DecoratorInvocation[]}|null)[] = [
{type: ChangeDetectorRef, },
{type: ElementRef, },
{type: Renderer, },
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'contentEl': [{ type: ContentChild, args: [NglFormInput, ] },],
'labelStr': [{ type: Input, args: ['label', ] },],
'labelTpl': [{ type: ContentChild, args: [NglFormLabelTemplate, ] },],
'error': [{ type: Input },],
};
};

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
